.custom-object-view-list {
    margin-bottom: 35px;
}

.custom-section-view {
    transition: background-color 400ms ease-in-out;
}

.custom-section-view:hover {
    background-color: #eff0af;
}


.custom-object-view-item {
    position: relative;
    transition: background-color 400ms ease-in-out;
}

.custom-object-view-item:hover {
    background-color: #c7e421;
}

.delete-custom-object-view-button {
    position: absolute;
    top: 0px;
    right: 0px;
    border-radius: 100%;
    background-color: #c8c6c6;
    transition: background-color 300ms ease-in-out;
}

.delete-custom-object-view-button:hover {
    background-color: #e66262;
}

.delete-custom-object-view-button:active {
    background-color: #de3535;
}

.save-object-view-button,
.cancel-object-view-button {
    width: 80px;
    font-weight: 600;
    border-radius: 25px;
    height: 27px;
}

.save-object-view-button {
    background-color: #4c9af3;
}

.save-object-view-button:hover {
    background-color: #2682eb;
}

.save-object-view-button:active {
    background-color: #0675f4;
}

.cancel-object-view-button {
    background-color: #e66262;
}

.cancel-object-view-button:hover {
    background-color: #ed4242;
}

.cancel-button:active {
    background-color: #de3535;
}

.view-title {
    font-size: 1.3em;
}

.view-duration {
    font-size: 0.9em;
}

.hidden {
    display: none;
}

.display-flex {
    display: flex;
}