/* This includes <ProfilePicture /> and <Address /> */
#PersonalDetails {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    height: 100%;
    box-sizing: border-box;
    border-right: 8px solid #302e2e;
    width: 100%;
}


.personal-details-form {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    width: 100%;
    margin-top: 20px;
    width: 100%;
    padding-bottom: 10px;
    transition: background-color 400ms ease-in-out;
}

.personal-details-form:hover {
    background-color: #ebc99d;
}

#ProfilePicture {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
    transition: background-color 400ms ease-in-out;
    box-sizing: border-box;
}

#ProfilePicture:hover {
    background-color: #f8a9a9;
    border: 5px solid black;
}

.upload-input-label-and-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    font-size: 1.5em;
    text-decoration: underline;
}

.profile-picture-function-title {
    align-self: center;
    padding-left: 5px;

}

.profile-picture {
    margin-top: 20px;
    width: 130px;
    transition: background-color 400ms ease-in-out
}

#ProfilePicture:hover .profile-picture {
    background-color: #2690c9;
    border: 4px solid black;
    user-select: none;
    touch-action: none;
}

.name {
    width: 100%;
    display: flex;
    position: relative;
    justify-content: center;
    font-size: 2.2em;
}

.nameText-wrapper {
    width: 70%;
    text-align: center;
    display: inline-block;
    overflow-wrap: break-word;
}

.phone-number {
    margin-top: 20px;
}

/* more specific selector to select email text-wrapper */
.email .innerText-wrapper.wrapper {
    width: 66%;
}

.details {
    width: 100%;
    display: flex;
    position: relative;
}

.details>img {
    padding-right: 10px;
    padding-left: 50px;
    width: 20px;
    height: 20px;
    position: relative;
    margin-right: 10px;
}

.details>.wrapper {
    width: 60%;
    display: inline-block;
    overflow-wrap: break-word;
}

#Address {
    margin-top: 30px;
    width: 100%;
    margin-bottom: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    transition: background-color 400ms ease-in-out;
    min-height: 50px;
}

#Address:hover {
    background-color: #9facf4;
}

.address-form {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    align-items: center;
    gap: 10px;
    margin-top: 10px;
}

.address-display {
    max-width: 92%;
    white-space: pre-line;
    overflow-wrap: break-word;
}

.address-display-wrapper {
    display: flex;
    justify-content: center;
    gap: 5px;
}

#address-icon {
    width: 20px;
    height: 20px;   
}

.address-textarea{
    font-family:'Times New Roman', Times, serif;
    font-size: 0.9em;
    resize: vertical;
}

.background-color-input-wrapper {
    display: flex;
    flex-direction: column;
    margin: 20px;
    padding: 20px;
    gap: 10px;
    transition: background-color 400ms ease-in-out;
}

.background-color-input-wrapper:hover {
    background-color: #aaa8a8;
}

#PersonalDetails:hover .background-color-input-wrapper {
    background-color: #aaa8a8;
    animation: color-input-fade-in 400ms ease-in-out;
}


@keyframes color-input-fade-in {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.change-background-color-title {
    text-align: center;
}

.reset-background-color-button {
    font-weight: 600;
    border-radius: 5px;
    width: 50%;
    align-self: center;
    height: 30px;
    border: 2px solid black;
    background-image: linear-gradient(to bottom right, #B2EBF2, #54b9db);
}

.reset-background-color-button:hover {
    background-image: linear-gradient(to bottom right, #54b9db, #B2EBF2);
}

.hidden {
    display: none;
}